<template>
  <div
    :class="classes"
    @mouseenter="onMouseToggle"
    @mouseleave="onMouseToggle"
    v-clickoutside="handleClose"
  >
    <div
      :class="[prefixCls + '-rel']"
      ref="reference"
      @click="handleClick"
      @mousedown="handleFocus(false)"
      @mouseup="handleBlur(false)"
    >
      <slot></slot>
    </div>
    <transition name="fade">
      <!-- visible is from popper -->
      <div
        ref="popper"
        v-show="visible"
        v-transfer-dom
        :data-transfer="transfer"
        :class="popperCls"
        :style="styles"
        @click="handleTransferClick"
        @mouseenter="onMouseToggle"
        @mouseleave="onMouseToggle"
      >
        <div :class="[prefixCls + '-content']">
          <div :class="[prefixCls + '-arrow']"></div>

          <div :class="[prefixCls + '-inner']" v-if="confirm">
            <div :class="[prefixCls + '-body']">
              <i name="confirm" class="iconfont icon-android-alert"></i>
              <div :class="[prefixCls + '-body-message']">
                <slot name="title">{{ title }}</slot>
              </div>
            </div>
            <div :class="[prefixCls + '-footer']">
              <h-button
                type="text"
                size="small"
                v-text="localeCancelText"
                @click.native="cancel"
              ></h-button>
              <h-button
                type="primary"
                size="small"
                v-text="localeOkText"
                @click.native="ok"
              ></h-button>
            </div>
          </div>

          <div :class="[prefixCls + '-inner']" v-if="!confirm">
            <div :class="[prefixCls + '-title']" v-if="showTitle" ref="title">
              <slot name="title">
                <div :class="[prefixCls + '-title-inner']">
                  {{ title }}
                </div>
              </slot>
            </div>
            <div :class="[prefixCls + '-body']">
              <div :class="[prefixCls + '-body-content']">
                <transition name="fade">
                  <template v-if="showPopper">
                    <slot name="content">
                      <div :class="[prefixCls + '-body-content-inner']">
                        {{ content }}
                      </div>
                    </slot>
                  </template>
                </transition>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import debounce from 'lodash/debounce'
import Popper from '@packages/Popper/index'
import PropTypes from '@hui/shared-utils/vue-types'
import TransferDom from '@hui/directives/transfer-dom'
import clickoutside from '@hui/directives/clickoutside'
import Locale from '@hui/shared-utils/vue-mixins/locale'

// TODO prefix replace
const prefixCls = 'u-poptip'

export default {
  name: 'Poptip',
  mixins: [Popper, Locale],
  directives: { clickoutside, TransferDom },
  props: {
    // false:  表示渲染的内容通过v-show来控制 true: 表示内容通过v-if来控制,每次点击都要重新渲染内容
    isNotCache: PropTypes.bool.def(false),
    trigger: PropTypes.oneOf(['click', 'focus', 'hover']).def('click'),
    placement: PropTypes.oneOf([
      'top',
      'top-start',
      'top-end',
      'bottom',
      'bottom-start',
      'bottom-end',
      'left',
      'left-start',
      'left-end',
      'right',
      'right-start',
      'right-end'
    ]).def('top'),
    title: PropTypes.string,
    content: PropTypes.string.def(''),
    width: PropTypes.sNumber,
    confirm: PropTypes.bool.def(false),
    okText: PropTypes.string,
    cancelText: PropTypes.string,
    transfer: PropTypes.bool.def(false),
    closeOutClick: PropTypes.bool.def(false),
    notResponding: PropTypes.bool.def(false),
    customTransferClassName: PropTypes.string,
    autoPlacement: PropTypes.bool.def(true),
    zIndex: PropTypes.any.def(600),

  },
  data () {
    return {
      prefixCls: prefixCls,
      showTitle: true,
      isInput: false,
      disableCloseUnderTransfer: false // transfer 模式下，点击 slot 也会触发关闭
    }
  },
  computed: {
    classes () {
      return [
        `${prefixCls}`,
        {
          [`${prefixCls}-confirm`]: this.confirm
        }
      ]
    },
    styles () {
      const style = {}

      if (this.width) {
        style.width = `${this.width}px`
      }
      return style
    },
    popperCls () {
      return [
        `${this.prefixCls}-popper`,
        {
          [`${this.customTransferClassName}`]: this.customTransferClassName
        }
      ]
    },
    localeOkText () {
      if (this.okText === undefined) {
        return this.t('i.poptip.okText')
      }
      return this.okText
    },
    localeCancelText () {
      if (this.cancelText === undefined) {
        return this.t('i.poptip.cancelText')
      }
      return this.cancelText
    }
  },
  methods: {
    handleClick () {
      if (this.notResponding) {
        return false
      }
      if (this.confirm) {
        this.showPopper = !this.showPopper
        return true
      }
      if (this.trigger !== 'click') {
        return false
      }
      this.showPopper = !this.showPopper
    },
    handleTransferClick () {
      if (this.transfer) this.disableCloseUnderTransfer = true
    },
    handleClose () {
      if (this.closeOutClick || this.notResponding) return
      if (this.disableCloseUnderTransfer) {
        this.disableCloseUnderTransfer = false
        return false
      }
      if (this.confirm) {
        this.showPopper = false
        return true
      }
      if (this.trigger !== 'click') {
        return false
      }
      this.showPopper = false
    },
    handleFocus (fromInput = true) {
      if (
        this.trigger !== 'focus' ||
        this.confirm ||
        (this.isInput && !fromInput) ||
        this.notResponding
      ) {
        return false
      }
      this.showPopper = true
    },
    handleBlur (fromInput = true) {
      if (
        this.trigger !== 'focus' ||
        this.confirm ||
        (this.isInput && !fromInput) ||
        this.notResponding
      ) {
        return false
      }
      this.showPopper = false
    },
    /**
     * 从 reference 移动到 popper 时不隐藏,防止抖动
     * 实际表现是鼠标从 reference 节点移动到 popper 节点时，popper 元素闪动
     * 通过 relatedTarget 修正该行为 https://developer.mozilla.org/zh-CN/docs/Web/API/MouseEvent/relatedTarget
     **/
    onMouseleaveCheck (e) {
      const check = (elem) => elem == e.relatedTarget || elem.contains(e.relatedTarget)
      if (check(this.$refs.reference) || check(this.$refs.popper)) {
        return
      }
      this.showPopper = false
    },
    onMouseToggle: debounce(
      function (e) {
        const isMouseenter = e.type == 'mouseenter'
        if (this.trigger !== 'hover' || this.confirm || this.notResponding) {
          return false
        }
        if (isMouseenter) {
          this.showPopper = true
          return
        }
        this.onMouseleaveCheck(e)
      },
      300,
      {
        leading: true
      }
    ),
    cancel () {
      this.showPopper = false
      this.$emit('on-cancel')
    },
    ok () {
      this.showPopper = false
      this.$emit('on-ok')
    },
    getInputChildren () {
      const $input = this.$refs.reference.querySelector('input')
      const $textarea = this.$refs.reference.querySelector('textarea')
      const $children = $input || $textarea
      if ($children) {
        $children.removeEventListener('focus', this.handleFocus, false)
        $children.removeEventListener('blur', this.handleBlur, false)
      }
      return $children
    }
  },
  mounted () {
    if (!this.confirm) {
      this.showTitle = this.$slots.title !== undefined || this.title
    }
    if (this.trigger === 'focus') {
      this.getInputChildren()
    }
  },
  beforeDestroy () {
    this.getInputChildren()
  }
}
</script>
